import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Index from '@/pages/index/index'
import Detail from '@/pages/detail/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      mate: {
        keepAlive: true
      }
    },
    {
      path: '/detail',
      name: 'detail',
      component: Detail,
      mate: {
        keepAlive: true
      }
    }
  ]
})



