<script>
	export default {
		name: 'index',
		data() {
			return {
        realAddressRepeat: false,
        registerAddressRepeat:false,
        showIndustry: false,
        isRealAddressList:false,
        historyAddress:false,
        showArea: false,
        orderActive: 2,
        industrys:['保险业','采矿','能源','餐饮','宾馆','电讯业','房地产','服务','服装业','公益组织','广告业','航空航天','化学','健康','保健','建筑业'],
        areas:['北京市','天津市','上海市','庆市','河北省','山西省','辽宁省','吉林省','黑龙江省','江苏省','浙江省','安徽省','福建省','江西省','山东省','河南省','湖北省','湖南省','广东省','海南省','四川省','贵州省','云南省','陕西省','甘肃省','青海省','台湾省','内蒙古自治区','广西壮族自治区','西藏自治区','宁夏回族自治区','新疆维吾尔自治区','香港特别行政区','澳门特别行政区'],
        isHidden1: true,
        isHidden2: true,
        showOptions: true,
        searchInfo:{
          companyInfo:'',
          industryId:'',
          areaId:'',
          pageSize:5,
          page:1,
          dateorder:true,
          moneyorder:false
        },
        total:1,
        companyList:[],
        repeatCompanyList: [],
        registerCompanyList:[],
        realAddressLists: [],
        companyHistoryAddressList:[]
			}
		},
		components: { },
		methods: {
      toLocation(longitude, latitude) {
        this.$router.push({path:"/", query:{longitude:longitude ,latitude: latitude}})
      },
      myCollection(company) {
        if(company.Collection === true) {
          this.cancelCollection(company)
        }else{
          this.entCollection(company)
        }
      },
      entCollection(company){
        let _this = this
        this.api.Post('index/entCollection',{
          companyId: company.companyId,
          companyName: company.companyName,
          industry: company.industry,
          latitude: company.latitude,
          legalPerson: company.legalPerson,
          longitude: company.longitude,
          phoneNum: company.phoneNum,
          realAddress: company.realAddress,
          region: company.region,
          registerAddress: company.registerAddress,
          registeredCapital: company.registeredCapital,
          reglatitude: company.reglatitude,
          reglongitude: company.reglongitude,
          startTime: company.startTime
        }).then(function(res){
          _this.$message(res.msg);
        })
      },
      cancelCollection(company){
        let _this = this
        this.api.Post('index/cancelCollection',{
          companyId: company.companyId,
          companyName: company.companyName
        }).then(function(res){
          _this.$message(res.msg);
        })
      },
      getHistoryAddress(company) {
        let _this = this
        this.api.Get('index/getCompanyHistoryAddressList',{
          companyId: company.companyId,
          companyName: company.companyName
        }).then(function(res){
          console.log(res)
          _this.historyAddress = true
          _this.companyHistoryAddressList = res.data.historyAddressList
        })
      },
      showAddressList(company) {
        let _this = this
        this.api.Get('list/getRealAddressList',{
          companyId: company.companyId,
          companyName: company.companyName
        }).then(function(res){
          _this.isRealAddressList = true
          var dataObj = eval('(' + res + ')')
          _this.realAddressLists = dataObj.data.realAddressList
        })
      },
      showRealAddressRepeat(company) {
        let _this = this
        this.api.Get('index/getRepeatComanyList',{
          realAddress: company.realAddress
        }).then(function(res){
          _this.realAddressRepeat = true
          _this.repeatCompanyList = res.data.repeatCompanyList
        })
      },
      showRegisterAddressRepeat(company) {
        let _this = this
        this.api.Get('list/getRegisterAddressList',{
          registerAddress: company.registerAddress
        }).then(function(res){
          _this.registerAddressRepeat = true
          _this.registerCompanyList = res.data.registerRepeatCompanyList
        })
      },
      handleCurrentChange(val) {
        this.searchInfo.page = val
        this.getSearchList()
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      getSearchList() {
        console.log(this.searchInfo)
        let _this = this
        this.api.Get('list/getSearchResultList',this.searchInfo).then(function(res){
          console.log(res)
          _this.total = parseInt(res.totalRecord)
          _this.companyList = res.data.companyList
        })
      },
      moneyOrder() {
        this.orderActive = 1
        this.searchInfo.dateorder=false
        this.searchInfo.moneyorder=true
        this.getSearchList()
      },
      timeOrder() {
        this.orderActive = 2
        this.searchInfo.dateorder=true
        this.searchInfo.moneyorder=false
        this.getSearchList()
      },
      closeIndustry() {
        this.showIndustry = false
        this.searchInfo.industry = ''
      },
      closeArea() {
        this.showArea = false
        this.searchInfo.area = ''
      },
      selectIndustryClick(item) {
        this.showIndustry = true
        this.searchInfo.industry = item
        this.getSearchList()
      },
      selectAreaClick(item) {
        this.showArea = true
        this.searchInfo.area = item
        this.getSearchList()
      }
    },
    activated:function(){
      this.getSearchList()
      console.log("aaa");
    }

	}
</script>
