        margin:0 auto;
        padding:20px 0;
        .search-th{
          display:inline-block;
          float:left;
          line-height:40px;
          font-size:14px;
          color:#333;
        }
        .search-input{
          display:inline-block;
          width:440px;
          height:40px;
          border:1px solid #34b1ff;
          border-radius:3px;
          box-sizing:border-box;
            input{
              float: left;
              width:380px;
              height:38px;
              border:0;
              padding:0 10px;
              border-radius:3px;
              outline: none;
            }
            .search-btn{
              display: inline-block;
              float: right;
              width: 38px;
              height: 38px;
              line-height:38px;
              text-align: center;
              font-size:20px;
              color:#45baff;
              cursor: pointer;
            }
        }
      }
    }
  .conditions-box{
    margin:0 30px;
    border-bottom:1px solid #cce1fc;
    .sel-th{
      float:left;
      line-height:30px;
      margin:20px 0 15px;
    }
    .selected-box{
      float:left;
      color: #43b6ff;
      margin:20px 0 15px 40px;

      label{
        display:inline-block;
        width:122px;
        height:28px;
        line-height:28px;
        border:1px solid #34b1ff;
        border-radius:28px;
        font-size:14px;
        text-align:center;
        margin-right:20px;

        i.icon-guanbi{
          margin-left:10px;
          font-size:12px;
          cursor: pointer;
        }
      }
    }
  }
  }


</style>



