<script>
  export default{
    name: 'index',
    data(){

      return {
        map: null,
        searchVal: '',
        showDetail: false,
        showEntlist: false,
        showCompanyList:true,
        showCompanyDetail: false,
        showAddressRepeat: false,
        showAddressHistory: false,
        isHidden:false,
        selectArea: '北京市',
        currentArea:'北京市',
        menubtnText: '收起列表',
        currentAreaSearchVal: '',
        mainArea: [
          '全国',
          '北京',
          '上海',
          '广州',
          '深圳',
          '成都',
          '天津',
          '南京',
          '武汉',
          '杭州',
          '重庆'

        ],
        areasObj:{
          areaDatas:[],
          others:[]
        },
        points:[],
        companyList:[],
        repeatCompanyList:[],
        companyHistoryAddressList:[],
        companyDetailInfo: null,
        companyId:''
      }
    },
    mounted(){
      this.init();
    },
    activated() {
      var query = this.$route.query
      console.log('100',query)
      if (!query.longitude) {
        this.init();
      }else {
        this.toLocal(query.longitude,query.latitude)
      }
    },
    methods: {
      myCollection(company) {
        if(company.Collection === true) {
          this.cancelCollection(company)
        }else{
          this.entCollection(company)
        }
      },
      entCollection(company){
        let _this = this
        this.api.Post('index/entCollection',{
          companyId: company.companyId,
          companyName: company.companyName,
          industry: company.industry,
          latitude: company.latitude,
          legalPerson: company.legalPerson,
          longitude: company.longitude,
          phoneNum: company.phoneNum,
          realAddress: company.realAddress,
          region: company.region,
          registerAddress: company.registerAddress,
          registeredCapital: company.registeredCapital,
          reglatitude: company.reglatitude,
          reglongitude: company.reglongitude,
          startTime: company.startTime
        }).then(function(res){
          _this.$message('操作成功');
        })
      },
      cancelCollection(company){
        let _this = this
        this.api.Post('index/cancelCollection',{
          companyId: company.companyId,
          companyName: company.companyName
        }).then(function(res){
          _this.$message('操作成功');
        })
      },
      toLocal(longitude, latitude) {
        console.log(latitude)
        var map = new BMap.Map("allmap");
        map.centerAndZoom(new BMap.Point(longitude, latitude), 11);
// 创建地理编码实例
        var myGeo = new BMap.Geocoder();
// 根据坐标得到地址描述
        myGeo.getLocation(new BMap.Point(longitude, latitude), function(result){
          if (result){

            var sContent =
              "<p style='color: #868686; font-size: 12px; line-height: 18px'>地址："+result.address+"</p>"
            var icon = new BMap.Icon('../static/img/3x24.png', new BMap.Size(24, 24));
            var point = new BMap.Point(longitude, latitude);
            var marker = new BMap.Marker(point, { icon: icon });
            var infoWindow = new BMap.InfoWindow(sContent, { offset: new BMap.Size(-10, -10) }); // 创建信息窗口对象

            map.addOverlay(marker);
            marker.addEventListener("mouseover", function() {
              this.openInfoWindow(infoWindow);
            });
            marker.addEventListener("mouseout", function() {
              this.closeInfoWindow(infoWindow);
            });
      }
        });


  },
      menuClick() {
        if (this.isHidden === true) {
          this.isHidden = false
          this.menubtnText = '收起列表'
        }else {
          this.isHidden = true
          this.menubtnText = '展开列表'
        }
      },
      getRepeatCompany(address) {
        let _this = this
        this.api.Get('index/getRepeatComanyList',{
          realAddress: address
        }).then(function(res){
          _this.showCompanyDetail = false
          _this.showAddressRepeat = true
          _this.showAddressHistory = false
          _this.repeatCompanyList = res.data.repeatCompanyList
        })
      },
      getHistoryAddress() {
        let _this = this
        this.api.Get('index/getCompanyHistoryAddressList',{
          companyId: this.companyId
        }).then(function(res){
          console.log(res)
          _this.showCompanyDetail = false
          _this.showAddressHistory = true
          _this.companyHistoryAddressList = res.data.historyAddressList
        })
      },
      searchList(str){
        var searchInfo = ''
        var areaName = ''
        if(str === 'current'){
          if (this.currentAreaSearchVal === ''){
            alert('请输入公司名称或地址')
            return
          }
          searchInfo = this.currentAreaSearchVal
          areaName = this.currentArea
        }else {
          if (this.searchVal === ''){
            alert('请输入公司名称或地址')
            return
          }
          searchInfo = this.searchVal
          areaName = this.selectArea
          console.log(areaName)
        }
        let _this = this
        this.api.Get('index/getCompanyList',{
          areaId: areaName,
          companyInfo: searchInfo
        }).then(function(res){
          _this.companyList = res.data.Datalist
          _this.showEntlist = true
          _this.isHidden = false
          _this.showCompanyList = true
          _this.showCompanyDetail = false
          _this.showAddressRepeat = false
          _this.showAddressHistory = false
          _this.initMap(areaName)
        })

      },
      initCity() {
        let _this = this
        this.api.Get('index/getAreaInfo').then(function(res){
          var dataObj = eval('(' + res + ')')
          _this.areasObj.areaDatas = dataObj.data.areaDatas
          _this.areasObj.others = dataObj.data.others
        })
      },
      selectAreaClick(areaName) {
        this.selectArea = areaName
        this.showDetail = !this.showDetail
        this.initMap(areaName)
      },
      topClick() {
        this.showDetail = !this.showDetail
      },
      init(){
        this.initMap("北京市")
        this.initCity()
      },
      getEntList(){
        this.$router.push({
          path:"/detail"
        });
        },
      getCompanyDetailInfo(companyItem) {
        this.companyId = companyItem.companyId
        let _this = this
        this.api.Get('index/getCompanyDetailInfo',{
          companyName: companyItem.companyName
        }).then(function(res){
          _this.showCompanyList = false
          _this.showAddressRepeat = false
          _this.showAddressHistory = false
          _this.showCompanyDetail = true
          _this.companyDetailInfo = res.data.companyDetailInfo
        })

      },
      initMap(AreaItem){
        let _this = this
        var map = new BMap.Map("allmap");    // 创建Map实例
        map.centerAndZoom(new BMap.Point(116.404, 39.915), 5);  // 初始化地图,设置中心点坐标和地图级别
        map.enableScrollWheelZoom();     //开启鼠标滚轮缩放
        this.map = map;
        var local = new BMap.LocalSearch(map, {
          renderOptions:{map: map}
        });
        local.search(AreaItem);
        this.companyList.forEach(function(item){
          var sContent =
            "<h3 style='font-size: 12px; color: #429eff; text-decoration: underline; line-height: 30px'>"+item.companyName+"</h3>" +
            "<p style='color: #868686; font-size: 12px; line-height: 18px'>地址："+item.realAddress+"</p>"
          var icon = new BMap.Icon('../static/img/3x24.png', new BMap.Size(24, 24));
          var point = new BMap.Point(item.longitude, item.latitude);
          var marker = new BMap.Marker(point, { icon: icon });
          var infoWindow = new BMap.InfoWindow(sContent, { offset: new BMap.Size(-10, -10) }); // 创建信息窗口对象

          map.addOverlay(marker);
          marker.addEventListener("mouseover", function() {
            this.openInfoWindow(infoWindow);
          });
          marker.addEventListener("mouseout", function() {
            this.closeInfoWindow(infoWindow);
          });
          marker.addEventListener("click", function() {
            _this.getCompanyDetailInfo(item)
          });
        })



      }
    }
  }
</script>
