  .showCompanyList h2 span:hover{
    color: #0794ff;
  }

  .showCompanyList h3 span:hover{
    color: #ff8739;
  }
 .showCompanyList h2 i{
   color: #ff490a;
 }
  .showCompanyList li h3{
    font-size: 12px;
    line-height: 20px;
    padding-left: 18px;
  }
.topStyle{
  transform: rotate(180deg);
}
.detail-box{
  width: 100%;
  height: 333px;
  padding: 0 15px;
  box-sizing: border-box;
  position: absolute;
  bottom: -343px;
  left: 0;
  background: #ffffff;
  box-shadow:0px 0px 10px 0 rgba(0,0,0,.5);
  border-radius: 5px;
}
