  }
  .detail-box h3 span{
  line-height: 50px;
  font-weight: 700;
  color: #333333;
  font-size: 12px;
    vertical-align: middle;
    float: left;
}
.detail-box  .input-box{
  width: 190px;
  overflow: hidden;
  position: relative;
float: left;
  margin-left: 47px;
  margin-top: 6px;
}
  .detail-box  .input-box input{
    width: 90%;
    border: 1px solid #d4d4d4;
    border-radius: 3px;
    height: 22px;
    outline: none;
    padding-right: 30px;
    box-sizing: border-box;
  }
  .detail-box  .input-box  .icon-search{
    font-size: 20px;
    width: 30px;
