    line-height: 32px;
  }
  .city-list-top span{
    cursor: pointer;
    color: #39a4ff;
    &:hover{
    text-decoration: underline;
     }
  }
  .city-list-bottom span{
    padding: 0 2px;
    color: #4d4d4d;
    cursor: pointer;
  &:hover{
     text-decoration: underline;
   }
  }
 .city-list-detail{
   height: 173px;
   box-sizing: border-box;
   padding-top: 12px;
   overflow-y: scroll;
  /*滚动条样式*/
  &::-webkit-scrollbar{width:6px;height:10px;}
  &::-webkit-scrollbar-thumb{ background:rgba(204,204,204,1); border-radius: 5px;}
 }
.city-list-detail-content{
  height: 300px;
}
  .city-list-detail-content li {
    overflow: hidden;
  }
  .city-list-detail-content li span{
    color: #1d97ff;
