    cursor: pointer;
    &:hover{text-decoration: underline;}
  }
.city-list-detail-content .detail-left{
  width: 40px;
  font-size: 20px;
  color: #cccccc;
  text-indent: 6px;
}
.city-list-detail-content .detail-middle{
 width: 60px;
}
  .city-list-detail-content .detail-middle span{
    padding: 0;
  }
.city-list-detail-content .detail-right{
}
.sanjiao{
  position: absolute;
  display: inline-block;
  background: url("../../assets/img/sanjiao_03.png");
  width: 19px;
  height: 9px;
  left: 27px;
  top: -9px;
}

</style>
<style>
  .BMap_pop>img{
    display: none;
